import React, { useState } from "react";
import { Box, Button, TextField, Typography, } from '@mui/material';
import { Link, useNavigate } from "react-router-dom";
import { login, loginWithGithub } from "../api/firebase";

export default function SignIn () {
  const [userInfo, setUserInfo] = useState({email:'', password:''});
  const navigate = useNavigate();

    const handleChange = e => {
      setUserInfo({...userInfo, [e.target.name]: e.target.value});
    }
    const handleSubmit = e => {
      e.preventDefault(); 
      if(userInfo.password.length <= 5){
        alert('  6  .')
        return;
      } else {
        login(userInfo);
        navigate('/');
      }
    }
    const handleGithub = () => {
      loginWithGithub();
      navigate('/');
    }
    return (
        <Box>
          <form onSubmit={handleSubmit}>
            <TextField type="email" name="email" value={userInfo.email} placeholder=" ..." onChange={handleChange} autoFocus /><br />
            <TextField type="password" name="password" value={userInfo.password} placeholder=" ..." onChange={handleChange} required /><br />
            <Button type="submit"></Button>
          </form>
          <Typography>  ?</Typography>
          <Link to='/signUp'> </Link>
          <Button onClick={handleGithub}> </Button>
        </Box>
    );
}