import React, { useState } from "react";
import { register, loginWithGithub } from '../api/firebase';
import { uploadImage } from "../api/cloudinary";
import Box from '@mui/material/Box';
import InputBase from '@mui/material/InputBase';
import { Button, Typography } from "@mui/material";
import { Link, useNavigate } from "react-router-dom";

export default function SignUp(){
    const [userInfo, setUserInfo] = useState({email:'', password:'', name:'', photo:''});
    const navigate = useNavigate();

      const handleChange = e => {
          setUserInfo({...userInfo, [e.target.name]: e.target.value});
      }
      const handleSubmit = e => {
          e.preventDefault();
          register(userInfo);
          navigate('/signIn');
      }
      const handleGithub = () => {
        loginWithGithub();
        navigate('/');
      }
      const handleUpload = e => {
        uploadImage(e.target.files && e.target.files[0])
          .then(url => setUserInfo({...userInfo, ['photo']: url}));
      }
    return(
        <Box>
            <form onSubmit={handleSubmit}>
                <InputBase type="email" name="email" value={userInfo.email} placeholder=""
                    onChange={handleChange} autoFocus required /><br />
                <InputBase type="password" name="password" value={userInfo.password} placeholder=""
                    onChange={handleChange} required /><br />
                <InputBase type="text" name="name" value={userInfo.name} placeholder=""
                    onChange={handleChange} required /><br />
                <input type="file" accpet="image/*" name="file" onChange={handleUpload} required/>
                <Button onClick={handleSubmit}> </Button>
            </form>
            <Typography>  ?</Typography>
            <Link to='/signIn'> </Link><br />
            <Button onClick={handleGithub}> </Button>
        </Box>    
    )
}